@import url('https://fonts.googleapis.com/css2?family=Montserrat+Alternates:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,400;0,500;1,400;1,500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Carrois+Gothic&display=swap');

* {
  cursor: crosshair;
}

:root {
  --primary-color: #c4cfaf;
  --angry-color: #c90f1e;
  --off-white: #dedede;
}

body {
  background-color: var(--off-white);
}

.profile-card__description {
  width: 100%;
  padding: 15px;
  border-radius: 18px;
  display: inline-block;
  background-color: white;
}

.profile-card__dislikes {
  width: 100%;
  padding: 15px;
  border-radius: 18px;
  margin-top: 15px;
  display: inline-block;
  background-color: white;
}

.profile-card__socials {
  width: 100%;
  padding: 15px;
  border-radius: 18px;
  margin-top: 15px;
  display: inline-block;
  background-color: white;
}

.profile-card__likes {
  width: 100%;
  padding: 15px;
  border-radius: 18px;
  margin-top: 15px;
  display: inline-block;
  background-color: white;
}

.profile-card__description__pfp {
  width: 20%;
  margin-left: 30px;
  display: inline-block;
}

.profile-card__description__pfp img {
  height: 100px;
  border-radius: 50px;
}

.profile-card__description__about {
  width: 70%;
  display: inline-block;
}

.profile-card__description__about__title {
  text-align: center;
  font-family: 'Montserrat Alternates', sans-serif;
  font-weight: 800;
  font-size: x-large;
  color: var(--primary-color);
  font-style: italic;
}

.profile-card__description__about__subtitle {
  text-align: center;
  font-family: 'Rubik', sans-serif;
  margin-top: 10px;
  font-size: medium;
}

.profile-card__description__about__pills {
  display: inline-grid;
  grid-template-columns: auto auto auto;
  margin-top: 10px;
}

.profile-card__description__about__pills .pill {
  background-color: var(--primary-color);
  padding: 7px;
  width: 110px;
  margin-right: 10px;
  text-align: center;
  font-family: 'Carrois Gothic', sans-serif;
  color: #fff;
  transition: transform 0.2s;
  font-size: medium;
  border-radius: 14px;
}

.profile-card__socials__links-grid {
  display: inline-grid;
  width: 100%;
  grid-template-columns: auto auto auto;
  grid-template-rows: auto;
  align-items: center;
  margin-top: 10px;
}

.profile-card__socials__links-grid .link {
  padding: 7px;
  margin-right: 10px;
  text-align: center;
  color: #fff;
  margin-top: 15px;
  border-radius: 14px;
  background-color: var(--primary-color);
  transition: transform 0.2s;
}

.profile-card__socials__links-grid .link:hover {
  transform: scale(1.05);
  cursor: pointer;
}

.profile-card__likes__label {
  background-color: var(--primary-color);
  padding: 7px;
  text-align: center;
  color: #fff;
  border-radius: 14px;
  font-family: 'Montserrat Alternates', sans-serif;
  font-weight: 800;
  font-size: x-large;
  font-style: italic;
}

.profile-card__socials__label {
  background-color: var(--primary-color);
  padding: 7px;
  text-align: center;
  color: #fff;
  border-radius: 14px;
  font-family: 'Montserrat Alternates', sans-serif;
  font-weight: 800;
  font-size: x-large;
  font-style: italic;
}

.profile-card__dislikes__label {
  background-color: var(--primary-color);
  padding: 7px;
  text-align: center;
  color: #fff;
  border-radius: 14px;
  font-family: 'Montserrat Alternates', sans-serif;
  font-weight: 800;
  font-size: x-large;
  font-style: italic;
}

.profile-card__likes_pg {
  text-align: center;
  font-family: 'Rubik', sans-serif;
  margin-top: 10px;
  font-size: medium;
}

.profile-card__dislikes_pg {
  text-align: center;
  font-family: 'Rubik', sans-serif;
  margin-top: 10px;
  font-size: medium;
}

.profile-card__description__about__pills .pill:hover {
  transform: scale(1.1);
}

.profile-card__highlight {
  color: var(--primary-color);
}

.profile-card__angry-highlight {
  color: var(--angry-color);
}

.err {
  background-color: var(--primary-color);
  padding: 7px;
  text-align: center;
  color: #fff;
  border-radius: 14px;
  font-family: 'Montserrat Alternates', sans-serif;
  font-weight: 800;
  font-size: x-large;
  font-style: italic;
}
